@use 'base/mixins' as m;
@use 'base/variables' as v;

.form{
    margin-top: 5rem;
    .field{
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        label{
            flex: 0 0 10rem;
        }
        input{
            font-size: 1.4rem;
            flex: 1;
            padding: 1rem;
            border: none;
            background-color: lighten($color: v.$gris, $amount: 40%);
        }
    }
}