@use 'base/mixins' as m;
@use 'base/variables' as v;

.sidebar{
    opacity: 0;
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;
    &.show{
        height: 100vh;
        padding: 2rem;
        opacity: 1;
    }
    &.hide{
        opacity: 0;
    }
    @include m.tablet{
        height: 100%;
        opacity: 1;
        padding: 2rem;
        background-color: v.$indigo;
    }
    .container-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2{
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }
        .close-menu{
            display: flex;
            justify-content: center;
            height: 5rem;
            width: 5rem;
            img{
                &:hover{
                    cursor: pointer;
                }
            }
            @include m.tablet{
                display: none;
            }
        }
    }
    .sidebar-nav{
        margin-top: 5rem;
        a{
            color: v.$blanco;
            display: block;
            padding: .7rem;
            transition-property: backgroung-color;
            transition-duration: 300ms;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 2.4rem;
            margin-bottom: 2rem;
            text-align: center;
            &:hover,
            &.active{
                background-color: darken($color: v.$indigo, $amount: 20%);
                border-radius: .5rem;
            }
            @include m.tablet{
                font-size: 2rem;
                margin-bottom: 1rem;
                text-align: left;
            }
        }
    }
    .close-session-mobile{
        margin-top: 10rem;
        .close-session{
            @include m.button(v.$naranja, v.$blanco)
        }
        @include m.tablet{
            display: none;
        }
    }
}