@use 'base/mixins' as m;
@use 'base/variables' as v;

.login{
    margin-top: 5rem;
    .uptask{
        @include m.textLinearGradient(v.$cyan);
    }
    input[type="submit"]{
        @include m.button(v.$cyan, v.$blanco);
    }
    .actions{
        a{
            color: v.$cyan;
        }
    }
}