@use 'base/mixins' as m;
@use 'base/variables' as v;

.modal {
    position: absolute;
    background-color: rgb(0 0 0 /.8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .form {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-timing-function: ease-in-out;
        transition-duration: 500ms;
        opacity: 0;
        border-radius: 1rem;

        legend{
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }

        .options{
            display: flex;
            justify-content: space-between;
        }

        .submit-new-task{
            @include m.button(v.$indigo, v.$blanco);
        }
        .close-modal{
            @include m.button(v.$naranja, v.$blanco);
        }

        &.animate {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.close{
            transform: translateY(-200%);
            opacity: 0;

        }
        input[type="text"]{
            border: 1px solid v.$gris;
        }
    }
}