@use 'base/mixins' as m;
@use 'base/variables' as v;

.filter{
    margin-top: 2rem;
    border-radius: .5rem;
    padding: 2rem;
    .filter-input{
        @include m.tablet{
            display: flex;
            align-items: center;
            gap: 3rem;
        }
    }
    h2{
        color: v.$indigo;
        font-size: 2.8rem;
        @include m.tablet{
            margin: 0;
        }
    }
    .field{
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;
        @include m.tablet{
            margin-bottom: 0;
        }
    }
    input[type="radio"]{
        accent-color: v.$indigo;
        width: 1.6rem;
        height: 1.6rem;
    }
}