@use 'base/mixins' as m;
@use 'base/variables' as v;

.container-new-task{
    display: flex;
    justify-content: center;
    .save-task{
        @include m.button(v.$cyan, v.$blanco)
    }
}
.list-tasks{
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    .empty-tasks{
        text-align: center;
    }
}
.task{
    padding: .5rem 2rem;
    border-bottom: 2px solid darken($color: v.$grisClaro, $amount: 5%);
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:last-of-type{
        border: none;
    }

    p{
        font-size: 1.4rem;
    }
    .options{
        display: flex;
        gap: 1rem;
        button{
            border: none;
            padding: .5rem 1rem;
            text-transform: uppercase;
            font-weight: normal;
            border-radius: .5rem;
            &:hover{
                cursor: pointer;
            }
        }
        .completa{
            background-color: v.$azul;
            color: v.$blanco;
            &:hover{
                background-color: darken($color: v.$azul, $amount: 5%);
            }
        }
        .pendiente{
            background-color: v.$naranja;
            color: v.$blanco;
            &:hover{
                background-color: darken($color: v.$naranja, $amount: 5%);
            }
        }
        .delete-task{
            background-color: v.$rojo;
            color: v.$blanco;
            &:hover{
                background-color: darken($color: v.$rojo, $amount: 5%);
            }
        }
    }
}