@use 'base/mixins' as m;
@use 'base/variables' as v;

.create{
    margin-top: 5rem;
    .uptask{
        @include m.textLinearGradient(v.$morado);
    }
    input[type="submit"]{
        @include m.button(v.$morado, v.$blanco);
    }
    .actions{
        a{
            color: v.$morado;
        }
    }
}