@use 'base/mixins' as m;
@use 'base/variables' as v;

.bar{
    display: none;
    
    @include m.tablet{
        background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: 1px 12px  9px -13px rgba($color: #000000, $alpha: .4);
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    p{
        margin: 0;
        font-size: 1.4rem;
        span{
            font-weight: 700;
            color: v.$indigo;
        }
    }
    .logout{
        @include m.button(darken(v.$indigo, 20%), v.$blanco);
        font-size: 1.4rem;
        padding: 1rem;
        border-radius: .5rem;
    }
}

.bar-mobile{
    background-color: v.$indigo;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.tablet{
        display: none;
    }
    h1{
        color: v.$blanco;
        margin: 0;
    }
    .menu{
        img{
            width: 4rem;
            &:hover{
                cursor: pointer;
            }
        }
    }
}