html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.page-description {
  text-align: center;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

/** Media Queries **/
.form {
  margin-top: 5rem;
}
.form .field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.form .field label {
  flex: 0 0 10rem;
}
.form .field input {
  font-size: 1.4rem;
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.actions {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .actions {
    display: flex;
    justify-content: space-between;
  }
}
.actions a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.actions a:last-of-type {
  margin-bottom: 0;
}

.alert {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alert.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alert.success {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.login {
  margin-top: 5rem;
}
.login .uptask {
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #066981;
  cursor: pointer;
}
.login .actions a {
  color: #0891B2;
}

