.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .content {
  padding: 2rem;
}
.dashboard .content .page-name {
  text-align: center;
  font-size: 2.8rem;
}
.dashboard .form input:not([type=submit]) {
  background-color: #FFFFFF;
}
.dashboard .form input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .dashboard .form input[type=submit] {
    width: auto;
  }
}
.dashboard .form input[type=submit]:hover {
  background-color: #352ca3;
  cursor: pointer;
}

.link {
  color: #4338CA;
}
.link:hover {
  color: #28217b;
}

.sidebar {
  opacity: 0;
  background-color: #4338CA;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.show {
  height: 100vh;
  padding: 2rem;
  opacity: 1;
}
.sidebar.hide {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #4338CA;
  }
}
.sidebar .container-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .container-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .container-sidebar .close-menu {
  display: flex;
  justify-content: center;
  height: 5rem;
  width: 5rem;
}
.sidebar .container-sidebar .close-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .container-sidebar .close-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  padding: 0.7rem;
  transition-property: backgroung-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.4rem;
  margin-bottom: 2rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.active {
  background-color: #28217b;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 2rem;
    margin-bottom: 1rem;
    text-align: left;
  }
}
.sidebar .close-session-mobile {
  margin-top: 10rem;
}
.sidebar .close-session-mobile .close-session {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .close-session-mobile .close-session {
    width: auto;
  }
}
.sidebar .close-session-mobile .close-session:hover {
  background-color: #c57f08;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .close-session-mobile {
    display: none;
  }
}

.bar {
