.empty-projects a {
  color: #4338CA;
}

.list-projects {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .list-projects {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .list-projects {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.list-projects .project {
  display: grid;
  grid-template-columns: 5fr 0.5fr;
}
.list-projects .project .project-li {
  background-color: #564ccf;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 0.5rem 0 0 0.5rem;
  transition: transform 0.2s;
}
.list-projects .project:hover {
  background-color: #3b31b8;
  cursor: pointer;
  border-radius: 0.5rem;
  transform: scale(1.1);
}
.list-projects .project .icons {
  background-color: #564ccf;
  min-height: 12rem;
  border-radius: 0 0.5rem 0.5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem;
  line-height: 2;
  transition: transform 0.2s;
}
.list-projects .project .icons svg {
  stroke: #FFFFFF;
}
.list-projects .project .icons svg:hover {
  stroke: #F59E0B;
}
.list-projects a {
  color: #FFFFFF;
}

.container-new-task {
  display: flex;
  justify-content: center;
}
.container-new-task .save-task {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
