  padding: 0;
  margin-top: 3rem;
}
.list-tasks .empty-tasks {
  text-align: center;
}

.task {
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #e4e7eb;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.task:last-of-type {
  border: none;
}
.task p {
  font-size: 1.4rem;
}
.task .options {
  display: flex;
  gap: 1rem;
}
.task .options button {
  border: none;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-weight: normal;
  border-radius: 0.5rem;
}
.task .options button:hover {
  cursor: pointer;
}
.task .options .completa {
  background-color: #2563EB;
  color: #FFFFFF;
}
.task .options .completa:hover {
  background-color: #1555e2;
}
.task .options .pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.task .options .pendiente:hover {
  background-color: #dd8e09;
}
.task .options .delete-task {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
}
.task .options .delete-task:hover {
  background-color: #900000;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .form {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-timing-function: ease-in-out;
  transition-duration: 500ms;
  opacity: 0;
  border-radius: 1rem;
}
.modal .form legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .form .options {
  display: flex;
  justify-content: space-between;
}
.modal .form .submit-new-task {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .modal .form .submit-new-task {
    width: auto;
  }
}
.modal .form .submit-new-task:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.modal .form .close-modal {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .modal .form .close-modal {
    width: auto;
  }
}
.modal .form .close-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .form.animate {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .form.close {
  transform: translateY(-200%);
  opacity: 0;
}
.modal .form input[type=text] {
  border: 1px solid #6b7280;
}

.filter {
  margin-top: 2rem;
  border-radius: 0.5rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .filter .filter-input {
    display: flex;
    align-items: center;
    gap: 3rem;
  }
}
.filter h2 {
  color: #4338CA;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .filter h2 {
    margin: 0;
  }
}
.filter .field {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .filter .field {
    margin-bottom: 0;
  }
}
.filter input[type=radio] {
  accent-color: #4338CA;
  width: 1.6rem;
  height: 1.6rem;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }

  .tagline,
.form label,
.login .actions a,
.create .actions a,
.forget .actions a,
.actions a,
.empty-projects a {
    color: #FFFFFF;
  }

  .page-description,
.form .field input,
.page-name,
.dashboard .link,
.list-tasks .empty-tasks {
    color: #f3f4f6;
  }

  .form .field input,
.dashboard .form input:not([type=submit]) {
    background-color: #4d4d67;
  }
  .form .field input::placeholder,
.dashboard .form input:not([type=submit])::placeholder {
    color: #9ca4b4;
  }

  .alert.error {
    background-color: #FFFFFF;
  }

  .bar-mobile,
.sidebar,
.bar {
    background-color: #1d1d27;
  }
  .bar-mobile .sidebar-nav a.active, .bar-mobile .sidebar-nav a:hover,
.sidebar .sidebar-nav a.active,
.sidebar .sidebar-nav a:hover,
.bar .sidebar-nav a.active,
.bar .sidebar-nav a:hover {
    background-color: #F59E0B;
  }
  .bar-mobile .logout,
.sidebar .logout,
.bar .logout {
    background-color: #F59E0B;
  }
  .bar-mobile .logout:hover,
.sidebar .logout:hover,
.bar .logout:hover {
    background-color: #c57f08;
  }
  .bar-mobile p,
.sidebar p,
.bar p {
    color: #F59E0B;
  }
  .bar-mobile p span,
.sidebar p span,
.bar p span {
    color: #f3f4f6;
  }

  .dashboard {
    background-color: #2c2c3b;
  }

  .list-projects .project .project-li,
.list-projects .project .icons {
    background-color: #585875;
  }

  .list-tasks .task {
    background-color: #424258;
    border: none;
  }
  .list-tasks .task p {
    color: #f3f4f6;
  }

  .filter h2, .filter label {
    color: #FFFFFF;
  }
  .filter input[type=radio] {
    accent-color: #F59E0B;
  }

  .dashboard .form input[type=submit] {
    background-color: #F59E0B;
  }

  .content p {
    color: #F59E0B;
  }

  .form.new-task {
    background-color: #424258;
  }
  .form.new-task legend {
    color: #f3f4f6;
  }
  .form.new-task input[type=submit] {
    background-color: #4338CA;
  }
}