@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard{
    background-color: v.$grisClaro;
    @include m.tablet{
        display: flex;
        height: 100vh;
    }
    .principal{
        flex: 1;
        overflow: scroll;
    }
    .content{
        padding: 2rem;
        .page-name{
            text-align: center;
            font-size: 2.8rem;
        }
    }
    .form{
        input:not([type="submit"]){
            background-color: v.$blanco;
        }
        input[type="submit"]{
            @include m.button(v.$indigo, v.$blanco);
            padding: 1rem;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.4rem;        
        }
    }
}
.link{
    color: v.$indigo;
    &:hover{
        color: darken($color: v.$indigo, $amount: 20%);
    }
}