@use 'base/mixins' as m;
@use 'base/variables' as v;

.forget{
    margin-top: 5rem;
    .uptask{
        @include m.textLinearGradient(v.$rosa);
    }
    input[type="submit"]{
        @include m.button(v.$rosa, v.$blanco);
    }
    .actions{
        a{
            color: v.$rosa;
        }
    }
}