@use 'base/mixins' as m;
@use 'base/variables' as v;

@media (prefers-color-scheme: dark){
    $grisOscuro: #2c2c3b;
    body{
        background-color: $grisOscuro;
    }
    .tagline,
    .form label,
    .login .actions a,
    .create .actions a,
    .forget .actions a,
    .actions a,
    .empty-projects a{
        color: v.$blanco;
    }
    .page-description,
    .form .field input,
    .page-name,
    .dashboard .link,
    .list-tasks .empty-tasks{
        color: v.$grisClaro;
    }
    .form .field input,
    .dashboard .form input:not([type=submit]){
        background-color: lighten($color: $grisOscuro, $amount: 15%);
        &::placeholder{
            color: darken($color: v.$grisClaro, $amount: 30%);
        }
    }
    .alert.error{
        background-color: v.$blanco;
    }
    .bar-mobile,
    .sidebar,
    .bar{
        background-color: darken($color: $grisOscuro, $amount: 7%);
        .sidebar-nav a{
            &.active,
            &:hover{
                background-color: v.$naranja;
            }
        }
        .logout{
            background-color: v.$naranja;
            &:hover{
                background-color: darken($color: v.$naranja, $amount: 10%);
            }
        }
        p{
            color: v.$naranja;
            span{
                color: v.$grisClaro;
            }
        }
    }
    .dashboard{
        background-color: $grisOscuro;
    }
    .list-projects .project .project-li,
    .list-projects .project .icons{
        background-color: lighten($color: $grisOscuro, $amount: 20%);
    }

    .list-tasks .task{
        background-color: lighten($color: $grisOscuro, $amount: 10%);
        border: none;
        p{
            color: v.$grisClaro;
        }
    }

    .filter{
        h2,label{
            color: v.$blanco;
        }
        input[type="radio"]{
            accent-color: v.$naranja;
        }
    }

    .dashboard .form input[type=submit]{
        background-color: v.$naranja;
    }
    .content p{
        color: v.$naranja;
    }
    .form.new-task{
        legend{
            color: v.$grisClaro;
        }
        background-color: lighten($color: $grisOscuro, $amount: 10%);
        input[type="submit"]{
            background-color: v.$indigo;
        }
    }
}