@use 'base/mixins' as m;
@use 'base/variables' as v;

.empty-projects{
    a{
        color: v.$indigo;
    }
}


.list-projects{
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include m.grid(1, 2rem);
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    @include m.desktop{
        @include m.grid(3, 2rem);
    }

    .project{
        display: grid;
        grid-template-columns: 5fr .5fr;
        .project-li{
            background-color: lighten($color: v.$indigo, $amount: 5%);
            min-height: 12rem;
            display: flex;
            align-items: center;
            padding: 2rem;
            line-height: 2;
            border-radius: .5rem 0 0 .5rem;
            transition: transform .2s;
            
        }
        &:hover{
            background-color: darken($color: v.$indigo, $amount: 5%);
            cursor: pointer;
            border-radius: .5rem;
            transform: scale(1.1);
        }
        .icons{
            background-color: lighten($color: v.$indigo, $amount: 5%);
            min-height: 12rem;
            border-radius: 0 .5rem .5rem 0;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 2rem;
            line-height: 2;
            transition: transform .2s;

            svg{
                stroke: v.$blanco;
                &:hover{
                    stroke: v.$naranja;
                }
            }
        }
    }
    a{
        color: v.$blanco;
    }
}